"use client";

import { useState } from "react";
import { X, Minus } from "lucide-react";

export default function ChatSupportPage() {
  const [isOpen, setIsOpen] = useState(true);
  const [isMinimized, setIsMinimized] = useState(false);
  const [messages, setMessages] = useState([
    { sender: "support", text: "Hi there! How can we assist you today?" },
  ]);
  const [input, setInput] = useState("");

  const handleSend = () => {
    if (input.trim()) {
      setMessages([...messages, { sender: "user", text: input }]);
      setInput("");
    }
  };

  return (
    <>
      {isOpen && (
        <div className="fixed bottom-4 left-4 bg-white w-80 rounded-xl shadow-lg z-50">
          {/* Header */}
          <div className="bg-blue-600 text-white flex justify-between items-center p-3 rounded-t-xl">
            <span className="font-semibold">ðŸ’¬ Chat Support</span>
            <div className="flex space-x-2">
              <button onClick={() => setIsMinimized(!isMinimized)}>
                <Minus className="w-5 h-5" />
              </button>
              <button onClick={() => setIsOpen(false)}>
                <X className="w-5 h-5" />
              </button>
            </div>
          </div>

          {!isMinimized && (
            <div className="flex flex-col h-96">
              {/* Messages */}
              <div className="flex-1 overflow-y-auto p-3 space-y-2 text-sm bg-gray-50">
                {messages.map((msg, i) => (
                  <div
                    key={i}
                    className={`p-2 rounded-lg ${
                      msg.sender === "user"
                        ? "bg-blue-100 text-right ml-auto"
                        : "bg-gray-200 text-left mr-auto"
                    } max-w-[80%]`}
                  >
                    {msg.text}
                  </div>
                ))}
              </div>

              {/* Input */}
              <div className="flex p-3 border-t">
                <input
                  type="text"
                  className="flex-1 border border-gray-300 rounded-lg p-2 text-sm"
                  placeholder="Type your message..."
                  value={input}
                  onChange={(e) => setInput(e.target.value)}
                  onKeyDown={(e) => e.key === "Enter" && handleSend()}
                />
                <button
                  onClick={handleSend}
                  className="ml-2 px-3 py-1 text-white bg-blue-600 rounded-lg text-sm"
                >
                  Send
                </button>
              </div>
            </div>
          )}
        </div>
      )}

      {!isOpen && (
        <button
          onClick={() => setIsOpen(true)}
          className="fixed bottom-4 left-4 bg-blue-600 text-white px-4 py-2 rounded-full shadow-lg z-50"
        >
          ðŸ’¬ Chat
        </button>
      )}
    </>
  );
}
